// Create custom element wrapper for Wix
class SophosicChatElement extends HTMLElement {
  constructor() {
    super();
    this.attachShadow({ mode: 'open' });
  }

  async connectedCallback() {
    const container = document.createElement('div');
    container.id = 'sophosic-chat-container-' + Math.random().toString(36).substr(2, 9);
    this.shadowRoot.appendChild(container);

    const config = {
      apiUrl: this.getAttribute('api-url') || 'https://n8n.sophosic.ai/webhook/e60704a9-52c1-47eb-9037-2344d6b56c21/chat',
      theme: {
        primary: this.getAttribute('primary-color') || '#5423E7',
        secondary: this.getAttribute('secondary-color') || '#1E1B3C'
      }
    };

    try {
      await this.loadScript('/sophia-web/js/app.js');
      
      // Wait for the module to be loaded
      setTimeout(() => {
        const SophosicChat = window.SophosicChat;
        if (SophosicChat?.mountWidget) {
          SophosicChat.mountWidget('#' + container.id, config);
        } else {
          console.error('SophosicChat mountWidget function not found', SophosicChat);
        }
      }, 100);
    } catch (err) {
      console.error('Failed to load chat widget:', err);
    }
  }

  loadScript(src) {
    return new Promise((resolve, reject) => {
      if (document.querySelector(`script[src="${src}"]`)) {
        resolve();
        return;
      }
      const script = document.createElement('script');
      script.src = src;
      script.onload = resolve;
      script.onerror = reject;
      document.head.appendChild(script);
    });
  }
}

// Only define if not already defined
if (!customElements.get('sophosic-chat-widget')) {
  customElements.define('sophosic-chat-widget', SophosicChatElement);
} 